<template>
  <div id="app">
    <router-view />
  </div>
</template>

<script>

export default {
  name: "App",
  components: {
  },
  data() {
    return {
      userRole: null, // 사용자 권한
      isAuthenticated: false, // 인증 상태
    };
  },
  created() {
    // 로컬 스토리지에서 사용자 권한 가져오기
    const storedRole = sessionStorage.getItem("userRole");

    if (storedRole) {
      // 권한이 설정된 경우 홈 페이지로 이동
      this.userRole = storedRole;
      this.isAuthenticated = true;
      this.$router.push("/home");
    } else {
      // 권한이 없는 경우 로그인 페이지로 이동
      this.isAuthenticated = false;
      this.$router.push("/");
    }
  },
};
</script>

<style>
@import './assets/fonts.css';

body {
  padding: 0;
  margin: 0;
  background-color: #f9f9f9;
}

#app {
  text-align: center;
}
</style>
