<template>
  <div class="display_box">
    <div v-if="generateQrCode">
      <canvas id="qrCanvas"></canvas>
    </div>
    <div v-else>
      <p>QR 코드가 아직 생성되지 않았습니다.</p>
    </div>
  </div>
</template>


<script>
import { QRCodeWriter, BarcodeFormat, EncodeHintType } from "@zxing/library";
import { nextTick } from "vue"; // DOM 업데이트 확인용. 이후 qr코드 생성

export default {
  name: "Qrdisplay",
  props: ["generateQrCode"],
  data() {
    return {
      qrCodeGenerated: false, // QR 코드가 생성되었는지 여부
    };
  },
  watch: {
    generateQrCode(newVal) {
      if (newVal && !this.qrCodeGenerated) {
        // QR 코드가 생성되지 않은 경우에만 생성
        this.createQrCode();
      }
    },
  },

  methods: {
    async createQrCode() {
      await nextTick(); // DOM 업데이트 보장
      const qrData = "출석 확인용 QR 코드"; // QR 코드에 포함될 데이터
      const canvasElement = document.getElementById("qrCanvas");
      if (canvasElement) {
        try {
          const qrCodeWriter = new QRCodeWriter();
          const hints = new Map();
          hints.set(EncodeHintType.CHARACTER_SET, "UTF-8");

          const matrix = qrCodeWriter.encode(
              qrData,
              BarcodeFormat.QR_CODE,
              300,
              300,
              hints
          );

          const context = canvasElement.getContext("2d");
          const canvasSize = matrix.getWidth();
          canvasElement.width = canvasSize;
          canvasElement.height = canvasSize;

          const tileSize = canvasElement.width / canvasSize;
          for (let x = 0; x < canvasSize; x++) {
            for (let y = 0; y < canvasSize; y++) {
              context.fillStyle = matrix.get(x, y) ? "#000" : "#fff";
              context.fillRect(x * tileSize, y * tileSize, tileSize, tileSize);
            }
          }

          this.qrCodeGenerated = true; // QR 코드 생성 상태 유지
          console.log("QR 코드 생성 완료!");
        } catch (error) {
          console.error("QR 코드 생성 중 오류 발생:", error);
        }
      } else {
        console.error("Canvas element not found");
      }
    },
  },

};

</script>

<style scoped>
canvas {
  margin-top: 20px;
  border: 1px solid #333;
  width: 300px; /* Canvas 크기 */
  height: 300px;
}

.display_box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  aspect-ratio: 1/1;

  border: 1px solid #ddd;
  border-radius: 20px;
  background-color: #fff;
}

h1 {
  font-size: 24px;
  margin-bottom: 20px;
  text-align: center;
}

button {
  padding: 10px 20px;
  font-size: 16px;
  color: white;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #0056b3;
}

canvas {
  margin-top: 20px;
  border: 1px solid #333;
}
</style>
