<template>
  <div class="class_info">
    <!-- 수업 정보 -->
    <div class="info_box">
      <div class="labels">
        <div>수업명</div>
        <div>교수명</div>
        <div>수업코드</div>
        <div>수업시간</div>
        <div>장소</div>
      </div>

      <div class="values" v-if="CurrentLecture">
        <div>{{ CurrentLecture.lectureName }}</div>
        <div>{{ CurrentLecture.teacherName }}</div>
        <div>{{ CurrentLecture.lectureCode }}</div>
        <div>{{ CurrentLecture.lectureTime }}</div>
        <div>{{ CurrentLecture.lecturePlace }}</div>
      </div>
    </div>

    <div class="line"></div>

    <!-- 주차 정보 -->
    <div class="info_box">
      <div class="labels">
        <div>주차</div>
        <div>수업날짜</div>
      </div>
      <div class="values" v-if="CurrentLecture">
        <div>{{ CurrentLecture.week }}</div>
        <div>{{ CurrentLecture.currentdate }}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ClassInfo",
  data() {
    return {
      CurrentLecture: null,
    };
  },
  async created() {
    try {
      const response = await fetch("/data/attend.json");
      if (response.ok) {
        const result = await response.json();
        if (result.status === "success") {
          this.CurrentLecture = result.data[0];
        }
      } else {
        console.error("Failed to fetch lecture data");
      }
    } catch (error) {
      console.error("Error fetching lecture data:", error);
    }
  },
};
</script>

<style scoped>
.class_info {
  max-width: 500px;
  margin: 0;
  border: 1px solid #D9D9D9;
  border-radius: 20px;
  padding: 20px;
  background: #fff;
}

.info_box {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  width: 100%;
  max-width: 500px;
  gap: 20px;
  padding: 10px;
  box-sizing: border-box;
}

.labels {
  display: flex;
  flex-direction: column;
  width:70px;
  gap: 10px;
}
.values {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.labels > div {
  padding: 0;
  text-align: left;
  font-size: 14px;
  color: #484C52;
}
.values > div {
  padding: 0;
  text-align: left;
  font-size: 14px;
  color: #000000;
}

.line {
  height: 1px;
  width: 100%;
  max-width: 500px;
  background-color: #ddd;
  margin: 5px auto;
}
</style>
