<template>
  <Navbar />
  <div class="attendance_page">
    <!-- 학생 페이지 -->
    <div v-if="isStudent" class="student_layout">
      <div class="atte_info">
        <Classinfo/>
        <Classbutton :userRole="userRole" />
      </div>
    </div>

    <!-- 선생님 페이지 -->
    <div v-else-if="isTeacher" class="teacher_layout">
      <div class="atte_info">
        <Classinfo/>
        <Classsetting />
        <Classbutton :userRole="userRole" @generateQr="handleGenerateQr" />
      </div>
      <Qrdisplay class="display_box" :generateQrCode="generateQrCode" />
    </div>
  </div>
</template>

<script>
import Navbar from "@/components/Navbar.vue";
import Classinfo from "@/components/Classinfo.vue";
import Classsetting from "@/components/Classsetting.vue";
import Classbutton from "@/components/Classbutton.vue";
import Qrdisplay from "@/components/Qrdisplay.vue";

export default {
  name: "Attendance",
  components: {
    Navbar,
    Classinfo,
    Classsetting,
    Classbutton,
    Qrdisplay,
  },
  data() {
    return {
      userRole: sessionStorage.getItem("userRole"),
      generateQrCode: false,

    };
  },
  computed: {
    isStudent() {
      return this.userRole === "student";
    },
    isTeacher() {
      return this.userRole === "teacher";
    },
  },
  methods: {
    handleGenerateQr(value) {
      console.log("QR 코드 요청 발생:", value);
      this.generateQrCode = value; // QR 코드 상태를 true로 설정
    },
  },
};
</script>

<style scoped>
.attendance_page {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15px;
  margin: 0 40px;
}
@media (max-width: 942px) {
  .attendance_page {
    margin: 20px 10px;
  }
}
.student_layout {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  width: 100%;
}
.teacher_layout {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 20px;
  width: 100%;
}

.atte_info {
  width:100%;
  max-width: 500px;
}

.display_box {
  width:100%;
  max-width: 500px;
}


@media (max-width: 800px) {
  .teacher_layout {
    flex-direction: column;
    align-items: center;
  }

  .atte_info {
    width: 100%;
    max-width: 500px;
  }

  .display_box {
    width:100%;
    max-width: 500px;
    margin-top: 20px;
  }
}
</style>
