<template>
  <div class="class_list">
    <div id="class_boxes">
      <p>Class</p>
      <div class="class_box" v-for="(lect, i) in lectures" :key="i">
        <div class="box_label"></div>
        <router-link :to="{ name: 'Attendance'}"> {{lect.lectureName}} </router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Classlist",
  data() {
    return {
      lectures: [], // 초기값
    };
  },
  async created() {
    try {
      const response = await fetch("/data/lecture.json");
      if (response.ok) {
        const result = await response.json();
        if (result.status === "success") {
          this.lectures = result.data;
        }
      } else {
        console.error("Failed to fetch lecture data");
      }
    } catch (error) {
      console.error("Error fetching lecture data:", error);
    }
  },
};
</script>

<style scoped>
.class_list {
  max-width: 920px;
  margin: 20px auto;
  box-sizing: border-box;
}

@media (max-width: 942px) {
  .class_list {
    margin: 20px 10px;
  }
}

.class_list p {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 10px;
}
#class_boxes {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  width: 100%;
  gap: 10px;
  margin: 0 auto;
}
#class_boxes > .class_box {
  display: flex;
  position: relative;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  background: #F7F9FC;
  border-radius: 20px;
  border: none;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  margin: 0;
}


#class_boxes > .class_box .box_label {
  position: absolute;
  top:0px;
  left:0px;
  width: 10px;
  height: 100%;
  background: #F3DCA9;
  flex-shrink: 0;
  border-radius: 20px 0px 0px 20px;
}
#class_boxes > .class_box a {
  text-align: left;
  flex: 1;
  padding:30px;
  margin: 0;
  font-size: 15px;
  font-weight: 500;
  align-self: center;
  color: #333;
  text-decoration: none;
}

</style>
