<template>
  <div class="class_setting">
    <div class="setting_box">
      <div class="setting_time" id="original_setting_time">
        <p>현재 출석 처리 시간</p>
        <div class="time_layout">
          <p>{{ currentStartTime }} - {{ currentEndTime }}</p>
          <p>(지각: {{ currentLateStartTime }} - {{ currentLateEndTime }})</p>
        </div>
      </div>

      <div class="setting_present_time">
        <p>출석 범위 설정</p>
        <div class="time-input">
          <input type="number" v-model="newStartHour" min="0" max="23" /> :
          <input type="number" v-model="newStartMinute" min="0" max="59" /> :
          <input type="number" v-model="newStartSecond" min="0" max="59" /> ~
          <input type="number" v-model="newEndHour" min="0" max="23" /> :
          <input type="number" v-model="newEndMinute" min="0" max="59" /> :
          <input type="number" v-model="newEndSecond" min="0" max="59" />
        </div>
      </div>

      <div class="setting_tardy_time">
        <p>지각 범위 설정</p>
        <div class="tardy_boxes">
          <label>
            <input type="radio" value="none" v-model="lateOption" /> 없음
          </label>
          <label>
            <input type="radio" value="5" v-model="lateOption" /> 5분
          </label>
          <label>
            <input type="radio" value="10" v-model="lateOption" /> 10분
          </label>
          <label>
            <input type="radio" value="custom" v-model="lateOption" /> 입력
            <input
                type="number"
                min="0"
                max="59"
                v-model="customLateMinutes"
                :disabled="lateOption !== 'custom'"

            /> 분
          </label>
        </div>
      </div>

      <div class="setting_time" id="updated_setting_time">
        <p>변경된 출석 처리 시간</p>
        <div class="time_layout">
          <p>{{ updatedStartTime }} - {{ updatedEndTime }}</p>
          <p>(지각: {{ updatedLateStartTime }} - {{ updatedLateEndTime }})</p>
        </div>
      </div>

      <label class="setting_save_time">
        <input type="checkbox" v-model="saveTime" /> 변경된 시간 기억하기
      </label>

    </div>
  </div>
</template>

<script>
export default {
  name: "ClassSetting",
  data() {
    return {
      // 원래 시간
      currentStartTime: "13:00:00",
      currentEndTime: "13:10:00",
      currentLateStartTime: "13:10:00",
      currentLateEndTime: "13:20:00",

      // 설정한 시간
      newStartHour: 13,
      newStartMinute: 0,
      newStartSecond:0,
      newEndHour: 13,
      newEndMinute: 10,
      newEndSecond:0,

      lateOption: "none", // none 기본.
      customLateMinutes: 0,

      // 변경된 시간
      updatedStartTime: "13:00:00",
      updatedEndTime: "13:10:00",
      updatedLateStartTime: "",
      updatedLateEndTime: "",

      saveTime: false,
    };
  },

  watch: {
    newStartHour: "updateAttendanceTime",
    newStartMinute: "updateAttendanceTime",
    newStartSecond: "updateAttendanceTime",
    newEndHour: "updateAttendanceTime",
    newEndMinute: "updateAttendanceTime",
    newEndSecond: "updateAttendanceTime",

    lateOption: "updateLateTime",
    customLateMinutes: "updateLateTime",
  },
  methods: {
    // 출석 범위 갱신 함수
    updateAttendanceTime() {
      this.updatedStartTime = `${this.newStartHour.toString().padStart(2, "0")}:
      ${this.newStartMinute.toString().padStart(2, "0")}:
      ${this.newStartSecond.toString().padStart(2, "0")}`;
      this.updatedEndTime = `${this.newEndHour.toString().padStart(2, "0")}:
      ${this.newEndMinute.toString().padStart(2, "0")}:
      ${this.newEndSecond.toString().padStart(2, "0")}`;
    },
    // 지각 범위 갱신 함수
    updateLateTime() {
      if (this.lateOption === "none") {
        this.updatedLateStartTime = "";
        this.updatedLateEndTime = "";
      }
      else {
        // 입력
        const lateMinutes = this.lateOption === "custom"
            ? this.customLateMinutes : parseInt(this.lateOption);

        const lateStartTime = new Date();
        lateStartTime.setHours(this.newEndHour);
        lateStartTime.setMinutes(this.newEndMinute);
        lateStartTime.setSeconds(this.newEndSecond);

        const lateEndTime = new Date(lateStartTime);
        lateEndTime.setMinutes(lateStartTime.getMinutes() + lateMinutes); // 지각 종료는 시작 시간 + 지각 설정된 시간

        this.updatedLateStartTime = `${lateStartTime.getHours().toString().padStart(2, "0")}:
            ${lateStartTime.getMinutes().toString().padStart(2, "0")}:
            ${lateStartTime.getSeconds().toString().padStart(2, "0")}`;

        this.updatedLateEndTime = `${lateEndTime.getHours().toString().padStart(2, "0")}:
            ${lateEndTime.getMinutes().toString().padStart(2, "0")}:
            ${lateEndTime.getSeconds().toString().padStart(2, "0")}`;
      }
    },
    applySettings() {
      if (this.saveTime) {
        localStorage.setItem("updatedStartTime", this.updatedStartTime);
        localStorage.setItem("updatedEndTime", this.updatedEndTime);
        localStorage.setItem("updatedLateStartTime", this.updatedLateStartTime);
        localStorage.setItem("updatedLateEndTime", this.updatedLateEndTime);
      }
      alert("변경 사항이 적용되었습니다!");
    },
  },
  mounted() {
    // 초기 값 계산
    this.updateAttendanceTime();
    this.updateLateTime();
  },
};
</script>

<style scoped>
.class_setting {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin:10px 0px;
}

.class_setting > p {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 10px;
  text-align: left;
}

.setting_box {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 8px;
  padding: 20px;
  margin: 0;
  max-width: 600px;
  border: 1px solid #ddd;
  border-radius: 20px;
  background-color: #fff;
  box-sizing: border-box;
}

.setting_time {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 10px;
  background-color: #ffffff;
  padding: 10px;
  border-radius: 20px;
  border: 1px solid #484C52;
}
.setting_time p {
  padding: 5px;
  font-size: 14px;
  font-weight: 400;
  margin: 0;
}

.time_layout {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
}

.time_layout p:last-child {
  font-size: 10px;
  font-weight: 400;
  color: #484C52;
}

.setting_present_time {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  margin:0 15px;
  font-size: 14px;
  gap: 10px;
}

.setting_present_time > p {
  font-size: 14px;
  font-weight: 500;
}


.setting_tardy_time {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  font-size: 14px;
  margin:0 15px;
  gap: 10px;
}
.setting_tardy_time > p {
  font-size: 14px;
  font-weight: 500;
}
.tardy_boxes {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
.tardy_boxes label {
  display:flex;
  align-items: center;
  gap:5px;
}

/* 화살표 지우기 */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}


input[type="number"] {
  text-align: center;
  width: 16px;
  padding: 5px;
  font-size: 14px;
}

@media (max-width: 942px) {
  .setting_time {
    padding:5px 10px;
  }
  .time_layout {
    flex-direction: column;
    gap: 0;
  }
}

.setting_save_time {
  display: flex;
  flex-direction: row;
  justify-content: center;
  font-size: 14px;

}
</style>
