<template>
  <div class="Main-view">
    <div class="views">
      <div class="view" id="time_view">
        <div id="top_time_view">
          <div>{{ CurrentDate }}</div>
          <div>{{ CurrentDay }}</div>
        </div>
        <div id="bottom_time_view">
          <div>{{ CurrentTime }}</div>
        </div>
      </div>

      <div class="view" id="class_view">
        <p>Now</p>
        <div class="class_box">
          <div class="box_label"></div>
          <p v-if="CurrentLecture">{{ CurrentLecture.lectureName }}</p>
          <p v-else>출석 가능한 수업이 없습니다.</p>
          <div  class="attendance_button">
            <router-link :to="{ name: 'Attendance'}">출석하기</router-link>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Mainview',
  data() {
    return {
      CurrentLecture: null,
      CurrentTime: this.getCurrentTime(),
      CurrentDate: this.getCurrentDate(),
      CurrentDay: this.getCurrentDay(),
    }
  },

  async created() {
    try {
      const response = await fetch("/data/attend.json");
      if (response.ok) {
        const result = await response.json();
        if (result.status === "success") {
          this.CurrentLecture = result.data[0];
        }
      } else {
        console.error("Failed to fetch lecture data");
      }
    } catch (error) {
      console.error("Error fetching lecture data:", error);
    }
  },

  methods: {
    getCurrentDate() {
      const now = new Date();
      const year = now.getFullYear();
      const month = String(now.getMonth() + 1).padStart(2, '0');
      const day = String(now.getDate()).padStart(2, '0');
      return `${year}.${month}.${day}`;
    },
    getCurrentTime() {
      const now = new Date();
      return now.toLocaleTimeString(
          'ko-KR',{
            hour12:false,
            hour:'2-digit',
            minute:'2-digit',
            second:'2-digit',
          }
      );
    },
    getCurrentDay() {
      const now = new Date();
      return now.toLocaleString('en', {weekday:'short'}).toUpperCase();
    },

  },

  mounted() {
    this.timer = setInterval(() => {
      this.CurrentDate = this.getCurrentDate();
      this.CurrentTime = this.getCurrentTime();
      this.CurrentDay = this.getCurrentDay();
    }, 1000);
  },
  beforeUnmount() {
    clearInterval(this.timer);
  },
}
</script>


<style>
.Main-view {
  max-width: 900px;
  margin: 20px auto;
  border: 1px solid #D9D9D9;
  border-radius: 20px;
  padding: 20px;
  background:#fff;
}
.Main-view .views {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 20px;
  margin: 10px;
}

.Main-view .views .view {
  flex: 1;
  text-align: center;
  padding: 10px;
  margin: 10px;
}
@media (max-width: 942px) {
  .Main-view {
    margin: 20px 10px;
  }
}
@media (max-width: 768px) {
  .Main-view .views {
    flex-direction: column;
    height: auto;
    margin: 0;
  }
  .Main-view .views .view {
    width: 100%;
    margin-bottom: 10px;
  }
}



#time_view {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  width: 320px;
  border-radius: 20px;
  padding: 20px;
  gap: 10px;
  margin: 0 auto;
}
#top_time_view {
  display: flex;
  text-align: left;
  align-items: baseline;
  width: auto;
  border-bottom: 1px solid black;
  margin-bottom: 10px;
}
#top_time_view > div {
  display: inline-block;
  font-size: 15px;
}
#top_time_view > div:first-child {
  font-size: 27px;
  margin-right: 5px;
  font-weight: 600;
}
#bottom_time_view {
  width: 72px;
  border-bottom: 1px solid black;
  text-align: left;
  margin-top: 10px;
  font-size: 18px;
  font-weight: 500;
}



#class_view {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  width: 320px;
  padding: 20px;
  gap: 10px;
  margin: 0 auto;
}
#class_view > p {
  font-weight: 600;
  margin-bottom: 10px;
}
#class_view > .class_box {
  display: flex;
  position: relative;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  background: #F7F9FC;
  padding: 10px;
  border-radius: 20px;
  border: none;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
}
#class_view > .class_box .box_label {
  position: absolute;
  top:0px;
  left:0px;
  width: 10px;
  height: 100%;
  background: #335DA9;
  flex-shrink: 0;
  border-radius: 20px 0px 0px 20px;
}
#class_view > .class_box p {
  text-align: left;
  flex: 1;
  padding-left: 10px;
  margin: 0;
  font-size: 13px;
  align-self: center;
}
#class_view > .class_box .attendance_button {
  width: 90px;
  height: 40px;
  background: #335DA9;
  box-shadow: 2px 2px 5px rgba(51, 93, 169, 0.25);
  border-radius: 15px;
  border: none;
  font-family: 'Pretendard';
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  color: #FFFFFF;
  display: flex;
  justify-content: center;
  align-items: center;
}
.attendance_button a,
.attendance_button router-link {
  color: #ffffff;
  text-decoration: none;

}
.attendance_button:hover {
  color:#ffffff;
}

</style>
