<template>
  <div class="attendance_button">
    <!-- 학생 버튼 -->
    <router-link v-if="isStudent" class="student_button"
                 :to="isAvailable ? { name: 'QRscan' } : null">
      출석하기
    </router-link>

    <!-- 교수자 버튼 -->
    <button v-else-if="isTeacher" class="teacher_button" @click="QRButtonClick">
      출석하기
    </button>
  </div>
</template>

<script>
export default {
  name: "ClassButton",
  data() {
    return {
      isAvailable: false,
    }
  },
  props: {
    userRole: {
      type: String,
      required: true,
    },

  },
  async created() {
    try {
      const response = await fetch("/data/atte-available.json");
      if (response.ok) {
        const result = await response.json();
        this.isAvailable = result.available;
      } else {
        console.error("Failed to fetch availability data");
      }
    } catch (error) {
      console.error("Error fetching availability data:", error);
    }
  },
  methods: {
    QRButtonClick() {
      if (this.isTeacher && this.isAvailable) {
        this.$emit("generateQr", true);
      } else {
        alert("권한이 없습니다.");
      }
    },
  },
  computed: {
    isStudent() {
      return this.userRole === "student";
    },
    isTeacher() {
      return this.userRole === "teacher";
    },
  },
};
</script>

<style scoped>
.attendance_button {
  max-width: 500px;
  margin: 20px auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.student_button {
  display: inline-block;
  width: 100%;
  padding: 20px;
  font-size: 16px;
  color: white;
  background: #335DA9;
  text-decoration: none;
  border: none;
  border-radius: 15px;
  cursor: pointer;
}

.student_button:hover {
  background-color: #3e70ca;
}
.student_button.disabled {
  background-color: gray;
  color: white;
  cursor: not-allowed;
  pointer-events: none; /* 클릭을 막음 */
}



.teacher_button {
  display: inline-block;
  width: 100%;
  padding: 20px;
  font-size: 16px;
  color: white;
  background: #335DA9;
  text-decoration: none;
  border: none;
  border-radius: 15px;
  cursor: pointer;
}

.teacher_button:hover {
  background-color: #3e70ca;
}

.teacher_button.disabled {
  background-color: gray;
  color: white;
  cursor: not-allowed;
}

</style>
